@import 'src/styles.module.scss';

.linksContainer {
  @include flex;
  justify-content: center;
  width: 100%;
  margin: .2rem 0 1rem;
}

.link {
  @include flex;
  padding: 0.5rem 1rem;
  margin: 0.5rem;
  border-radius: 1rem;
  text-decoration: none;
  background-color: $grey;
  color: white !important;
  text-align: center;
  white-space: nowrap;
}

.linkLogo {
  height: 1rem;
  margin-right: 0.5rem;
}