@import 'src/styles.module.scss';

.authorsContainer {
  width: 100%;
  margin-bottom: .5rem;
}

.list {
  margin: 1rem;
  @include flex;
  justify-content: center;

  &.multipleInstitutes > :not(:first-child) {
    border-left: 2px solid $grey;
  }
}

.author {
  @include flex;
  padding: 0 .5rem;
  font-size: 1.2rem;
  text-decoration: none;
  color: $honolulu_blue;
  text-align: center;
  white-space: nowrap;
}

.institute{
  padding: 0 .5rem;
  font-size: 1.2rem;
}